.adminPage {
  height: 20rem;
}

.database-asegurados {
  margin: 3rem auto;
}

.contenedor-botones {
  display: flex;
  justify-content: space-between;
}

.asegurados {
  margin-top: 3rem;
  width: 100%;
  border-spacing: 0;
  border: 1px solid #000;
}
.asegurados thead {
  border: 1px solid #000;
  background-color: #3A7CA5;
}
.asegurados thead th {
  border: 1px solid #000;
  padding: 1rem;
  color: #FDFFFC;
}
.asegurados thead th:last-of-type svg {
  width: 2.5rem;
}
.asegurados td {
  border: 1px solid #000;
  text-align: center;
}
.asegurados td:last-of-type {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}
.asegurados td:last-of-type svg {
  width: 2rem;
}